@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400;1,500&display=swap");

* {
  @apply font-[kanit];
}
:root {
  --primary-blue: #00d8ff;
  --primary-purple: #6a00ff;
}

@layer base {
  h1 {
    @apply text-4xl font-extrabold;
  }

  li {
    @apply xs:mt-5 p-4 hover:bg-blue-600 rounded-full xs:ml-3 xs:text-xl
    py-2 bg-gray-900 mr-4 hover:rounded-xl transition-all duration-200 ease-linear hover:-translate-y-2;
  }

  button {
    @apply xs:py-2
    bg-gradient-to-r from-[var(--primary-purple)] to-[var(--primary-blue)] py-3 px-7 rounded-3xl ml-4;
  }
}

@layer components {
  .tooltip-box {
    @apply absolute scale-0 bg-gray-900 w-auto p-2 m-2 min-w-max right-[-100px] rounded-md shadow-md text-white text-xs transition-all duration-150 origin-center top-0;
  }
  .card-div {
    @apply hover:scale-90 cursor-pointer shadow-md shadow-cyan-500 transition-transform hover:animate-pulse hover:bg-gradient-to-t from-blue-700;
  }

  @layer utilities {
    xs {
      @apply text-xs;
    }
  }
}
